import * as React from "react"
import { Link } from "gatsby"

const CourseCard = ({ course }) => {
    return (
        <Link to={`/courses/${course.slug}/`} className="no-underline">
            <article className="group cursor-pointer bg-white rounded-lg overflow-hidden editorial-shadow hover:shadow-xl transition-shadow">
                <div className="relative aspect-[16/9] overflow-hidden bg-[var(--color-editorial-light-bg)]">
                    {course.thumbnail ? (
                        <img
                            src={course.thumbnail}
                            alt={course.title}
                            className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                        />
                    ) : (
                        <div className="w-full h-full flex items-center justify-center text-[var(--color-editorial-grey)]">
                            <span style={{ fontSize: "var(--text-2xl)" }}>ðŸ“š</span>
                        </div>
                    )}
                </div>

                <div className="p-6">
                    <div className="mb-3 flex items-center gap-3 flex-wrap">
                        <span
                            className="inline-block px-3 py-1 bg-[var(--color-editorial-accent)] text-white rounded-full"
                            style={{ fontSize: "var(--text-xs)" }}
                        >
                            {course.totalModules} Modules
                        </span>
                        {course.level && (
                            <span
                                className="text-[var(--color-editorial-grey)]"
                                style={{ fontSize: "var(--text-xs)" }}
                            >
                                {course.level}
                            </span>
                        )}
                        {course.duration && (
                            <span
                                className="text-[var(--color-editorial-grey)]"
                                style={{ fontSize: "var(--text-xs)" }}
                            >
                                {course.duration}
                            </span>
                        )}
                    </div>

                    <h3
                        className="mb-3 text-[var(--color-editorial-black)] group-hover:text-[var(--color-editorial-accent)] transition-colors"
                        style={{ fontWeight: 600, fontSize: "var(--text-xl)" }}
                    >
                        {course.title}
                    </h3>

                    {course.description && (
                        <p
                            className="text-[var(--color-editorial-grey)] line-clamp-3"
                            style={{ fontSize: "var(--text-sm)", lineHeight: "1.6" }}
                        >
                            {course.description}
                        </p>
                    )}
                </div>
            </article>
        </Link>
    )
}

export default CourseCard
